const editorOptions = {
  data () {
    return {
      editorOptions: {
        // width: 1000,
        height: '800px',
        menus: [
          'head',  // 
          'bold',  // 
          'italic',  // 
          'underline',  // 
          'strikeThrough',  // 
          'foreColor',  // 
          'backColor',  // 
          'link',  // 
          'list',  // 
          'justify',  // 
          'quote',  // 
          'emoticon',  // 
          'image',  // 
          'table',  // 
          // 'video',  // 
          // 'code',  // 
          'insertCode',  // 
          'clearStyle', // 
          'clearFormat', // 
          'clearAll', // 
          'undo',  // 
          'redo',  // 
          'fullscreen' // 
        ],
        pasteFilterStyle: true,  // 
        uploadImgServer: 'https://up-z2.qbox.me/',
        uploadFileName: 'file',
        uploadImgMaxLength: 1,
        uploadImgParams: {
          token: ''   //  encode  encode
        },
        uploadImgParamsWithUrl: true,
        uploadImgHooks: {
          // success: (xhr, editor, result) => {
          //   // 
          //   // xhr  XMLHttpRequst editor result 
          //   // console.log(result)
          // },
          // fail: (xhr, editor, result) => {
          //   // console.log(result)
          // },
          customInsert: (insertImg, result/*, editor*/) => {
            // console.log(insertImg)
            let url = `http://p0ry9w0d5.bkt.clouddn.com/${result.key}`
            insertImg(url)
          }
        }
      }
    }
  }
}

export default editorOptions



